<template>
  <div class="d-flex flex-column h-50">
    <div class="row border-bottom py-1">
      <h5 class="col my-auto">Файлы</h5>
      <div class="col text-right">
        <button
          class="btn btn-sm btn-info mr-1"
          @click="$refs.file.click()">
          +
        </button>
        <input type="file" class="d-none" ref="file" @change="download">
      </div>
    </div>
    <div class="pt-1 d-flex flex-column overflow-auto">
      <draggable v-model="files" @change="onMoved">
        <div v-for="(file, i) in files" :key="file.id" class="position-relative">
          <img
            :src="file.img.src"
            class="img-fluid bg-white mb-2"
            alt="">
          <font-awesome-layers class="fa-lg close-icon" @click="deleteFile(i)">
            <font-awesome-icon icon="circle" :style="{ color: 'Tomato' }" />
            <font-awesome-icon icon="times" transform="shrink-6" :style="{ color: 'white' }" />
          </font-awesome-layers>
        </div>
      </draggable>
    </div>
  </div>
</template>

<script>
import draggable from 'vuedraggable';
import toastr from 'toastr';

export default {
  components: { draggable },
  data() {
    return {
      files: [],
    };
  },
  computed: {
    filesInit() { return this.$store.state.files; },
  },
  watch: {
    filesInit(files) {
      this.files = files.map(file => ({ ...file }));
    },
  },
  methods: {
    onMoved(e) {
      if (e.moved) {
        const { oldIndex, newIndex } = e.moved;
        this.$store.commit('changeFilesOrder', { oldIndex, newIndex });
      }
    },
    download() {
      const name = this.$refs.file.files[0].name.split('.');
      if (!['jpg', 'jpeg', 'png'].includes(name[name.length - 1])) {
        toastr.warning('Пока можно только jpg и png');
        this.$refs.file.value = null;
        return;
      }
      const input = this.$refs.file;
      if (input.files && input.files[0]) {
        const reader = new FileReader();
        reader.onload = () => {
          this.$store.commit('addFiles', [reader.result]);
          this.$refs.file.value = null;
        };
        reader.readAsDataURL(input.files[0]);
      }
    },
    deleteFile(i) {
      this.$store.commit('deleteFile', i);
    },
  },
};
</script>

<style lang="scss">
.close-icon {
  position: absolute;
  top: 0.5rem;
  right: 0.5rem;
  cursor: pointer;
  transition: opacity 0.3s ease;
  &:hover {
    opacity: 0.8;
  }
}
</style>
