<template>
  <div class="fabric">
    <canvas ref="fabric"></canvas>
  </div>
</template>

<script>
import { fabric } from 'fabric';
import { mapState } from 'vuex';

export default {
  props: ['id', 'active'],
  data() {
    return {
      images: [],
      grid: [],
      startPoint: [],
      dopScale: {},
      fileTypeRotate: {},
      customSize: {},
      customRotate: {},
      level: {
        fileId: {},
        objectId: {},
      },
      deleted: [],
      paddingRect: null,
    };
  },
  computed: {
    ...mapState([
      'files', 'maxPixelSize',
    ]),
    template() {
      return this.$store.getters.templates.find(t => t.id === this.id);
    },
    dotsStartPoint() { return this.template.startPoint; },
    dotsGrid() { return this.template.grid; },
    pixelSize() { return this.template.pixelSize; },
    width() { return this.template.width; },
    height() { return this.template.height; },
    canvas() { return this.template.canvas; },
    sizeStr() { return this.template.sizeStr; },
    padding() { return this.template.padding; },
    kX() { return this.pixelSize.width / this.width; },
    kY() { return this.pixelSize.height / this.height; },
    paddingRectSize() {
      if (this.padding === null) return null;
      if (!this.pixelSize.width || !this.pixelSize.height) return null;
      const left = (this.pixelSize.width / this.width) * this.padding;
      const top = (this.pixelSize.height / this.height) * this.padding;
      return {
        left,
        top,
        width: this.pixelSize.width - left * 2 - 1,
        height: this.pixelSize.height - top * 2 - 1,
      };
    },
    files() {
      const { step } = this.template;
      return this.$store.state.files.map((file) => {
        const scale = (step.x * this.kX) / file.img.width < (step.y * this.kY) / file.img.height
          ? (step.x * this.kX - 1) / file.img.width : (step.y * this.kY - 1) / file.img.height;
        return {
          ...file,
          width: step.x * this.kX,
          height: step.y * this.kY,
          scale,
        };
      });
    },
  },
  watch: {
    sizeStr() { this.setCanvasSize(); },
    active(nv) {
      if (nv) return;
      this.canvas.getObjects().forEach((object) => {
        if (['dots', 'padding', 'paddingLogo'].includes(object.id)) {
          this.canvas.remove(object);
        }
      });
      this.canvas.renderAll();
    },
    pixelSize() {
      if (!this.images.length && process.env.NODE_ENV === 'development') {
        const objects = this.canvas.getObjects();
        this.images = this.canvas.getObjects()
          .filter(o => o.id === 'images');
        this.canvas.initialize(this.$refs.fabric);
        this.canvas.add(...objects);
      }
    },
    dotsGrid() {
      this.setPaddingRect();
      this.renderImages();
    },
    files() {
      this.renderImages();
    },
  },
  mounted() {
    if (!this.canvas) {
      const canvas = new fabric.Canvas(this.$refs.fabric, {
        selection: false,
        uniScaleTransform: true,
        width: 0,
        height: 0,
        backgroundColor: 'white',
      });
      this.$store.commit('setCanvas', { id: this.id, canvas });
      this.setEvents();
    }
    if (this.maxPixelSize.width && this.maxPixelSize.height) {
      this.setCanvasSize();
    } else {
      const unwatch = this.$watch('maxPixelSize', (size) => {
        if (size.width && size.height) {
          unwatch();
          this.setCanvasSize();
        }
      });
    }
    this.setGrid();
  },
  methods: {
    setCanvasSize() {
      let size = {};
      const { width, height } = this.maxPixelSize;
      const col = width / height;
      const canvas = this.width / this.height;
      if (col > canvas) {
        size = { height: height * 0.96, width: (height / this.height) * this.width };
      } else {
        size = { height: (width / this.width) * this.height, width };
      }
      this.canvas.setDimensions(size);
      this.$store.commit('setPixelSize', { id: this.id, pixelSize: size });
      this.setPaddingRect();
    },
    setGrid() {
      this.$store.commit('setGrid', { forAll: false, id: this.id });
      if (this.id === 3) {
        setTimeout(() => {
          this.$store.commit('addFiles', ['tmpImages/1.png', 'tmpImages/2.jpg', 'tmpImages/4.png']);
        }, 100);
      }
    },
    setPaddingRect() {
      if (!this.paddingRectSize) return;
      if (this.paddingRect) {
        this.paddingRect.set({ ...this.paddingRectSize });
        this.paddingRect.setCoords();
      } else {
        this.paddingRect = new fabric.Rect({
          ...this.paddingRectSize,
          originX: 'left',
          originY: 'top',
          visible: false,
        });
        this.canvas.add(this.paddingRect);
      }
    },
    renderImages() {
      if (!this.files.length) return;
      if (this.images.length) this.canvas.remove(...this.images);
      this.images = [];
      if (!this.dotsGrid.length) return;
      const grid = this.clone(this.dotsGrid);
      const startPoint = this.clone(this.dotsStartPoint);
      if (!this.grid.length) {
        this.grid = this.clone(grid);
        this.startPoint = this.clone(startPoint);
      }
      const ids = this.files.map(file => file.id);
      const k = Math.round(100 / ids.length);
      const idsRow = new Array(k).fill(ids).reduce((acc, val) => acc.concat(val), []);
      let start = Math.round((k * ids.length) / 2);
      if (idsRow[start] !== ids[0]) {
        start = idsRow.slice(0, start).lastIndexOf(ids[0]);
      }
      let lastX;
      let startCopy = start;
      grid.slice(startPoint[0]).forEach((row, y) => {
        if (y === 0) {
          const rowStart = startCopy - startPoint[1];
          const idsRowPart = idsRow.slice(rowStart, rowStart + row.length);
          this.drawId(row, idsRowPart, y, rowStart);
          lastX = row[startPoint[1]].coords.cleanX;
          return;
        }
        let x = row.findIndex(col => col.coords.cleanX > lastX);
        if (x < 0) {
          startCopy -= 1;
          x = row.length - 1;
        }
        const rowStart = startCopy - x;
        const idsRowPart = idsRow.slice(rowStart, rowStart + row.length);
        this.drawId(row, idsRowPart, y, rowStart);
        lastX = row[x].coords.cleanX;
      });
      if (startPoint[0]) {
        startCopy = start;
        lastX = grid[startPoint[0]][startPoint[1]].coords.cleanX;
        grid.slice(0, startPoint[0]).reverse().forEach((row, y) => {
          let x = row.length - 1
            - row.slice().reverse().findIndex(col => col.coords.cleanX < lastX);
          if (x === row.length) {
            startCopy += 1;
            x = 0;
          }
          const rowStart = startCopy - x;
          const idsRowPart = idsRow.slice(rowStart, rowStart + row.length);
          this.drawId(row, idsRowPart, -(y + 1), rowStart);
          lastX = row[x].coords.cleanX;
        });
      }
      this.images = this.checkIntersects(this.images);
      this.canvas.add(...this.images);
    },
    drawId(row, idsRow, y, rowStart) {
      row.forEach((col, x) => {
        const file = this.files.find(f => f.id === idsRow[x]);
        const imgSrc = file.img.cloneNode();
        const dopScale = file.id in this.dopScale ? this.dopScale[file.id] : 1;
        const id = `${y}${rowStart + x}`;
        const customSize = id in this.customSize ? this.customSize[id] : 1;
        const level = this.level.objectId[id] || this.level.fileId[file.id] || 1;
        const img = new fabric.Image(imgSrc, {
          id,
          name: 'images',
          level,
          fileId: file.id,
          scaleInit: file.scale,
          left: row[x].coords.x * this.kX,
          top: row[x].coords.y * this.kY,
          scaleX: file.scale * dopScale * customSize,
          scaleY: file.scale * dopScale * customSize,
          angle: this.customRotate[id] || this.fileTypeRotate[file.id] || 0,
          opacity: +!this.deleted.includes(id),
          originX: 'center',
          originY: 'center',
          selectable: true,
          hoverCursor: 'pointer',
          lockMovementX: true,
          lockMovementY: true,
          lockScalingFlip: true,
          lockUniScaling: true,
          centeredScaling: true,
        });
        this.images.push(img);
      });
    },
    setEvents() {
      this.canvas.on('object:scaled', (o) => {
        const levels = Object.values(this.level.fileId)
          .concat(Object.values(this.level.objectId));
        const level = levels.length ? Math.max(...levels) + 1 : 2;
        if (!o.e.ctrlKey) {
          const dopScale = this.dopScale[o.target.fileId] || 1;
          this.customSize[o.target.id] = o.target.scaleX / (o.target.scaleInit * dopScale);
          this.level.objectId[o.target.id] = level;
          o.target.set({ level });
        } else {
          this.$set(this.dopScale, o.target.fileId, o.target.scaleX / o.target.scaleInit);
          this.level.fileId[o.target.fileId] = level;
          this.canvas.getObjects().forEach((object) => {
            if (object.name !== 'images' || object.fileId !== o.target.fileId) return;
            object.scale(o.target.scaleX);
            object.set({ level });
          });
        }
        this.checkIntersects();
      });
      this.canvas.on('object:rotated', (o) => {
        const levels = Object.values(this.level.fileId)
          .concat(Object.values(this.level.objectId));
        const level = levels.length ? Math.max(...levels) + 1 : 2;
        if (!o.e.ctrlKey) {
          this.customRotate[o.target.id] = o.target.angle;
          this.level.objectId[o.target.id] = level;
          o.target.set({ level });
        } else {
          this.$set(this.fileTypeRotate, o.target.fileId, o.target.angle);
          this.canvas.getObjects().forEach((object) => {
            if (object.name !== 'images' || object.fileId !== o.target.fileId || object.id in this.customRotate) return;
            object.rotate(o.target.angle);
            object.set({ level });
          });
        }
        this.checkIntersects();
      });
      window.addEventListener('keyup', (e) => {
        if (!this.active || !this.canvas.getActiveObject()) return;
        if (e.keyCode === 46) {
          const object = this.canvas.getActiveObject();
          if (!this.deleted.includes(object.id) && object.opacity) {
            this.deleted.push(object.id);
            object.set({ opacity: 0 });
          } else if (this.deleted.includes(object.id)) {
            this.deleted.splice(this.deleted.indexOf(object.id), 1);
          }
          this.canvas.renderAll();
          this.checkIntersects();
        }
      });
    },
    scaleImages() {},
    checkIntersects(images) {
      let objects = images || this.canvas.getObjects();
      objects = objects.sort((a, b) => {
        if (a.level > b.level) return -1;
        if (a.level < b.level) return 1;
        return 0;
      });
      const invisiblesId = [];
      objects.forEach((obj1, i1) => {
        if (obj1.name !== 'images' || invisiblesId.includes(obj1.id)) return;
        if (this.deleted.includes(obj1.id)) {
          invisiblesId.push(obj1.id);
          return;
        }
        if ([0, 1, 3].includes(this.id) && !obj1.isContainedWithinObject(this.paddingRect)) {
          invisiblesId.push(obj1.id);
          return;
        }
        objects.forEach((obj2, i2) => {
          if (i2 <= i1 || obj1.level === obj2.level) return;
          if (obj2.name !== 'images' || invisiblesId.includes(obj2.id)) return;
          if (this.deleted.includes(obj2.id)) {
            invisiblesId.push(obj2.id);
            return;
          }
          if (obj1.intersectsWithObject(obj2)) {
            invisiblesId.push(obj2.id);
          }
        });
      });
      objects.forEach((obj) => {
        if (obj.name !== 'images') return;
        if (invisiblesId.includes(obj.id)) {
          obj.set({ opacity: 0 });
        } else {
          obj.set({ opacity: 1 });
        }
      });
      this.canvas.renderAll();
      return objects;
    },
    addFile() {},
    clone(o) { return JSON.parse(JSON.stringify(o)); },
  },
};
</script>

<style lang="scss">
.fabric {
  .canvas-container {
    display: flex;
    align-self: center;
  }
  transform-origin: 0 0;
}
</style>
